*{box-sizing:border-box}#root{min-height:100vh;display:flex;flex-direction:column}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}body{margin:0;font-family:Lustria;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#353535;font-size:18px;background:#8FC1E3}h1,h2,h3,h4,h5,h6{font-family:'Montserrat', sans-serif}@media only screen and (min-width: 600px){body{font-size:24px}h1{font-size:2.5em}}

.maintenance{text-align:center}li{list-style:none}header a{text-decoration:none;color:#8FC1E3}main{background:#8FC1E3;margin:0;flex-grow:1;padding:2vh 5vw}main>section>h1{margin:5vh auto 10vh;text-align:center}main>section>h1>span{background-color:#636058;padding:10px 20px;box-shadow:0px 10px 8px rgba(0,0,0,0.3);box-shadow:0px 10px 5px #31708E;box-shadow:0px 10px 18px #31708E;color:white}@media only screen and (min-width: 600px){main>section>h1{margin:15vh auto 20vh}main>section>h1>span{padding:40px 80px}main{padding:2vh 10vw}}

.nav-menu{background:#31708E;flex-grow:0;flex-shrink:1;flex-basis:calc(100vw - 15vmax);max-width:calc(100vw - 15vmax);display:flex;flex-wrap:wrap;z-index:1;margin:0 auto;padding:4vh 2.2vw;border-radius:10% / 50%}.nav-link{margin:10px;flex:1 1 auto;text-align:center;font-size:calc(5px + 3vmin)}.nav-link a{text-decoration:none;border-bottom:3px solid #8FC1E3}.nav-link a:hover{color:#5085A5;border-color:#5085A5}.nav-link .isActive{border-color:#F7F9FB}.fadeIn{-webkit-animation:fadeIn 1s both;animation:fadeIn 1s both}.fadeOut{-webkit-animation:fadeOut 1s both;animation:fadeOut 1s both}@-webkit-keyframes fadeIn{0%{-webkit-transform:translateY(-30vh);transform:translateY(-30vh);opacity:0;background-color:#8FC1E3}100%{-webkit-transform:translateY(0);transform:translateY(0);opacity:1;background-color:transparent}}@keyframes fadeIn{0%{-webkit-transform:translateY(-30vh);transform:translateY(-30vh);opacity:0;background-color:#8FC1E3}100%{-webkit-transform:translateY(0);transform:translateY(0);opacity:1;background-color:transparent}}@-webkit-keyframes fadeOut{0%{opacity:1;background-color:transparent}100%{-webkit-transform:translateY(-30vh);transform:translateY(-30vh);opacity:0;background-color:#8FC1E3}}@keyframes fadeOut{0%{opacity:1;background-color:transparent}100%{-webkit-transform:translateY(-30vh);transform:translateY(-30vh);opacity:0;background-color:#8FC1E3}}@media screen and (min-width: 600px){.nav-menu{margin:0;margin-left:auto}}

.header{color:#5085A5;background:#31708E;box-shadow:33px 0 33px #31708E;position:sticky;z-index:2;top:0}.logo-container{position:absolute;flex:0 0 22vw;z-index:2}.logo-container img{width:90%;margin:1vh 0;width:22vw;min-width:15vh;max-width:15vmax;background-color:#31708E;border-bottom-left-radius:51%;border-bottom-right-radius:47%;margin-left:-10px}.nav-btn-container{flex:1 0 88vw;height:15vh;display:flex}.nav-menu-btn{margin-left:auto;margin-top:auto;background:transparent;box-shadow:0px 0px 0px transparent;border:0px solid transparent;text-shadow:0px 0px 0px transparent;color:#8FC1E3;font-size:7vmin;cursor:pointer}.nav-menu-btn:hover{background:transparent;box-shadow:0px 0px 0px transparent;border:0px solid transparent;text-shadow:0px 0px 0px transparent;color:#F7F9FB}.nav-menu-btn:active{outline:none;border:none}.nav-menu-btn:focus{outline:0}.isActive{color:#F7F9FB}.isActive:hover{color:#8FC1E3}.grow{-webkit-animation:grow 1s both;animation:grow 1s both}.shrink{-webkit-animation:shrink 1s both;animation:shrink 1s both}@-webkit-keyframes grow{0%{height:15vh}100%{height:calc(30vh + 5px + 10vmin)}}@keyframes grow{0%{height:15vh}100%{height:calc(30vh + 5px + 10vmin)}}@-webkit-keyframes shrink{0%{height:calc(30vh + 5px + 10vmin)}100%{height:15vh}}@keyframes shrink{0%{height:calc(30vh + 5px + 10vmin)}100%{height:15vh}}@media only screen and (min-width: 600px){@-webkit-keyframes grow{0%{height:15vh}100%{height:calc(30vh)}}@keyframes grow{0%{height:15vh}100%{height:calc(30vh)}}@-webkit-keyframes shrink{0%{height:calc(30vh)}100%{height:15vh}}@keyframes shrink{0%{height:calc(30vh)}100%{height:15vh}}}

footer{height:10.5vh;position:sticky;bottom:0;flex-shrink:0;display:flex;justify-content:center}footer div{flex:0 0 20%;justify-content:space-between;display:flex;background-color:#31708E;border-radius:15%/ 48%;box-shadow:5px 2px 1px 0 #5085A5 inset,-5px -2px 1px 0 #5085A5 inset,-4px 0px 5px 0 #F7F9FB inset,4px 0px 5px 0 #F7F9FB inset,-7px 4px 1px 0 #636058 inset,7px -4px 1px 0 #636058 inset;margin:5px}footer div a{color:#F7F9FB;margin:auto 2vw;font-size:5vh;padding:2px 7px 0}footer div a:hover{color:#8FC1E3}

address{display:flex;max-width:90vw;word-break:break-word}address table{flex:0 1 auto;table-layout:fixed;border-spacing:10px 20px;max-width:90vw;margin:auto}address table td{background:#F7F9FB;box-shadow:2px 2px #31708E;text-align:center;padding:15px 15px}address table td:first-child{white-space:nowrap}address table td a{color:#31708E;text-decoration:none;border-bottom:2px solid #31708E}address table td a:hover{color:#8FC1E3;text-shadow:0.5px 0.5px 0px #F7F9FB;border-bottom:2px solid #8FC1E3}address table td svg{margin-left:2px;-webkit-transform:scale(0.7);transform:scale(0.7)}

.photo{width:70vmin;height:62vmin;margin:auto}.photo img{height:100%;width:100%;border-bottom-color:#f7f9fb;border-left-color:rgba(247,249,251,0.8);border-right-color:rgba(247,249,251,0.8);border-top-color:rgba(247,249,251,0.7)}.frame{background-color:#636058;border:solid 5.5vmin;border-top-color:rgba(247,249,251,0.7);border-right-color:rgba(247,249,251,0.8);border-bottom-color:#f7f9fb;border-left-color:rgba(247,249,251,0.8);border-radius:2px;box-shadow:0 0 5px 0 rgba(0,0,0,0.25) inset,0 5px 10px 5px rgba(0,0,0,0.25);position:relative;text-align:center;padding:15px}.frame:before{position:absolute;top:-3.5vmin;bottom:-3.5vmin;left:-3.5vmin;right:-3.5vmin;box-shadow:0 2px 5px 0 rgba(0,0,0,0.25) inset;content:''}.frame:after{position:absolute;top:-1vmin;bottom:-1vmin;left:-1vmin;right:-1vmin;box-shadow:0 2px 5px 0 rgba(0,0,0,0.25);content:''}.newspaper p:first-of-type{font-size:150%;font-family:Lato;color:#31708E}.newspaper p:first-of-type::first-letter{font-size:150%}.signature p:last-of-type{font-size:150%;font-weight:600}@media only screen and (orientation: landscape){.photo{height:60vmin;width:68vmin;float:left;margin:80px 2ch 1ch 1ch}}@media only screen and (min-width: 600px) and (orientation: landscape){.photo{height:28vmax;width:30vmax;margin:220px 2ch 1ch 1ch}.newspaper p:first-of-type{font-size:235%}.newspaper p:first-of-type::first-letter{font-size:235%}.frame{border-width:2.5vmax}.frame:before{top:-1.75vmax;bottom:-1.75vmax;left:-1.75vmax;right:-1.75vmax}.frame:after{top:-1vmax;bottom:-1vmax;left:-1vmax;right:-1vmax}#bio-text-wrapper{margin-top:20vh}}

.img-wrapper{position:relative;height:100%}.img-wrapper img{width:100%;position:aboslute;height:100%}.img-wrapper:hover{-webkit-transform:scale(0.95, 0.95);transform:scale(0.95, 0.95);transition:linear 0.1s}.project-link-container{position:absolute;top:0;width:100%;height:100%;display:flex;opacity:0;flex-wrap:wrap;background-color:#F7F9FB;align-items:center;justify-content:space-around;padding:0 8%}.project-link-container a{flex:1 1 100%;margin:0 auto;display:none;color:#31708E;white-space:normal;text-align:center}.project-link-container a:first-of-type{margin-top:1.9vw;font-size:6.5vw}.project-link-container a:last-of-type{font-size:12vw}.project-link-container a sup>svg{-webkit-transform:scale(0.7);transform:scale(0.7)}@media only screen and (min-width: 600px){.project-link-container a:first-of-type{margin-top:1.9vw;font-size:2.5vw}.project-link-container a:last-of-type{font-size:4vw}}@media (pointer: coarse){.project-link-container{background-color:transparent;opacity:1}.project-link-container a{display:block;color:#F7F9FB;background:#31708E;border-radius:10%/50%;margin-left:auto;margin-right:auto;padding:10px}.project-link-container a:last-of-type{padding:0;display:flex;justify-content:center;background-color:transparent}.project-link-container a:last-of-type svg{background-color:#31708E;border-radius:50%}}@media (hover: hover){.project-link-container>a:hover{transition:linear 0.2s;-webkit-transform:scale(1.1);transform:scale(1.1)}.project-link-container:hover{opacity:0.8;-webkit-transform:scale(1.05, 1.05);transform:scale(1.05, 1.05);transition:linear 0.4s}.project-link-container:hover a{display:block}}

#project-grid{display:grid;margin:auto;grid-gap:5vh;gap:5vh;grid-auto-rows:1fr}@media only screen and (min-width: 600px){#project-grid{grid-template-columns:repeat(3, 1fr);grid-gap:2vmin;gap:2vmin}#project-grid>div:first-of-type{grid-area:1 / 1 / 2 span / 2 span}#project-grid>div:first-of-type .project-link-container>a:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}#project-grid>div:first-of-type a:first-of-type{margin-top:1.9vw;font-size:6.1vw}#project-grid>div:first-of-type a:last-of-type{font-size:8vw}#project-grid div:last-of-type{grid-column:-3 / -2}}

#resume{display:flex;flex-wrap:wrap;margin:auto;justify-content:space-between}#resume h1,#resume h2{flex:1 1 100%;text-align:center}#resume h2{margin:0}#resume h2:last-of-type{margin-bottom:30px}#resume a{color:#636058;text-shadow:0.5px 0.5px #31708E}#resume a:hover{color:#31708E}#resume div{margin:1.8vh 2vw}#resume svg{margin-left:20px}#proficiencies{display:flex;flex-wrap:wrap;padding-left:0}#proficiencies li{flex:1 0 auto;padding:2px 5px;background:#31708E;border-bottom:3px solid rgba(247,249,251,0.4);border-top:3px solid #f7f9fb;border-left:3px solid rgba(247,249,251,0.8);border-right:3px solid rgba(247,249,251,0.6);text-align:center;color:#F7F9FB}@media only screen and (min-width: 600px){#resume div{flex:1 1 40%}}

